<template>
  <div class="medicalNewsTop">
    <div class="medicalTopArea">
      <div class="topAreaBlock" @click="gotoArticleList(Global.MENU_ID_NEWS_OPINIONS)">
        <span>Opinions</span>
        <div class="showMoreImgArea">
          <img src="@/assets/medyImages/contents-more-small.png" srcset="@/assets/medyImages/contents-more-small@2x.png 2x, @/assets/medyImages/contents-more-small@3x.png 3x"/>
        </div>
      </div>
      <div class="topAreaBlock" @click="gotoArticleList(Global.MENU_ID_NEWS_MYMEDICALNEWS)">
        <span>My</span>
        <div class="showMoreImgArea">
          <img src="@/assets/medyImages/contents-more-small.png" srcset="@/assets/medyImages/contents-more-small@2x.png 2x, @/assets/medyImages/contents-more-small@3x.png 3x"/>
        </div>
      </div>
    </div>
    <PickUpArticles ref="pickUpArticles" :menuId="menuId" @setListScrollTop="setListScrollTop"/>
    <pickUpUsers ref="pickUpUsers" :menuId="menuId" @setListScrollTop="setListScrollTop"/>
    <div class="opinionsImgArea" :style="'background-image: linear-gradient(to bottom, rgba(191, 214, 232, 0.5), rgba(191, 214, 232, 0.5)),url(' + topOpinionsBackGroundImg + ');'">
      <p class="opinions-firstP">Opinions</p>
      <p class="opinions-secondP">-</p>
    </div>
    <articleList ref="articleListOpinions" :menuId="Global.MENU_ID_NEWS_OPINIONS" @setListScrollTop="setListScrollTop"/>
    <div class="myMedicalNewsImgArea" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + topMyMedicalNewsBackGroundImg + ');'">
      <p class="myMedicalNews-firstP">My</p>
      <p class="myMedicalNews-secondP"></p>
    </div>
    <articleList :isListCanExist="isNewsFeedSetting" ref="articleListMyMedicalNews" :menuId="Global.MENU_ID_NEWS_MYMEDICALNEWS" @setListScrollTop="setListScrollTop"/>
    <div class="newsFeedSettingText" v-if="!isNewsFeedSetting && isGotFeedFinished">
      <p>My</p>
      <p class="textMarginCol">200</p>
      <span>>My </span>
      <div class="settingImg">
      <img src="@/assets/medyImages/setting_small.png" srcset="@/assets/medyImages/setting_small@2x.png 2x, @/assets/medyImages/setting_small@3x.png 3x"/>
      </div>
      <span> </span>
    </div>
  </div>
</template>

<script>
import PickUpArticles from "@/components/cmptsClinical/PickUpArticles.vue"
import PickUpUsers from "@/components/cmptsClinical/PickUpUsers.vue"
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import topOpinionsBackGroundImg from "@/assets/medyImages/mediaclOpinionsTop.png"
import topMyMedicalNewsBackGroundImg from "@/assets/medyImages/myMedicalNewsTop.png"

export default {
  name: 'medicalNewsTop',
  prop: {
  },
  data: function () {
    return {
      isNewsFeedSetting: false,
      isGotFeedFinished: false,
      menuId: this.Global.MENU_ID_NEWS,
      topOpinionsBackGroundImg: topOpinionsBackGroundImg,
      topMyMedicalNewsBackGroundImg: topMyMedicalNewsBackGroundImg
    }
  },
  components: {
    PickUpArticles,
    PickUpUsers,
    ArticleList
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackkeep-allivecacherefresh
    } else {
      // 
      this.init();
    }
  },
  methods: {
    init: function () {
      var _this = this;
      //TODO AJAX GET ISSETNEWSFEED
      setTimeout(function(){
        _this.isNewsFeedSetting = false;
        _this.isGotFeedFinished = true;
      }, 1000);
    },
    setListScrollTop: function () {
      // pulltoscrolltop
      this.$emit('setListScrollTop');
      this.$refs.pickUpUsers.setScrollX();
    },
    gotoArticleList: function (menuId = null) {
      // 
      this.setListScrollTop();
      if(menuId == null){
        this.$router.push({ name: 'articleList', params: { menuId: this.menuId } });
      } else if(menuId == this.Global.MENU_ID_NEWS_OPINIONS) {
        this.$router.push({ name: 'medicalOpinions'});
      } else if(menuId == this.Global.MENU_ID_NEWS_MYMEDICALNEWS){
        this.$router.push({ name: 'medicalNews'});
      }
      
    },
    refresh: function (loaded) {
      this.init();
      this.$refs.pickUpArticles.getArticleByTab(loaded);
      this.$refs.pickUpUsers.getUserInfosByTab(loaded);
      this.$refs.articleListOpinions.getArticleList(loaded);
      if(typeof(this.$refs.articleListMyMedicalNews) != "undefined"){
        this.$refs.articleListMyMedicalNews.getArticleList(loaded);
      }
    }
  }
}
</script>

<style scope>
.medicalNewsTop .medicalTopArea {
  height: 48px;
  width: 100%;
  position: relative;
  background-color: var(--white-four);
}

.medicalNewsTop .medicalTopArea .topAreaBlock {
  width: 50%;
  height: 48px;
  display: inline-block;
  box-shadow: inset -1px 0 0 0 var(--white-three);
  position: relative;
  vertical-align: top;
}

.medicalNewsTop .medicalTopArea .topAreaBlock span {
  height: 20px;
  font-size: 14px;
  font-weight: 300;
  color: var(--black);
  line-height: 48px;
  margin-left: 12px;
}

.medicalNewsTop .medicalTopArea .topAreaBlock .showMoreImgArea {
  display: inline-block;
  position: absolute;
  width: 16px;
  height: 16px;
  overflow: hidden;
  right: 12px;
  top: 16px;
}

.medicalNewsTop .medicalTopArea .topAreaBlock .showMoreImgArea img {
  width: 16px;
}

.medicalNewsTop .opinionsImgArea,
.medicalNewsTop .myMedicalNewsImgArea{
  display: inline-block;
  overflow: hidden;
  width: 100%;
  height: 90px;
  background-blend-mode: color;
  background-color: rgba(51, 51, 51, 0.6);
  vertical-align: top;
  background-size: cover;
  background-repeat: no-repeat;
}

.medicalNewsTop .opinionsImgArea .opinions-firstP{
  height: 29px;
  font-size: 20px;
  font-weight: 500;
  line-height: 29px;
  color: var(--white-two);
  position: relative;
  margin: 11px auto 5px 12px;
}

.medicalNewsTop .opinionsImgArea .opinions-secondP{
  height: 36px;
  font-size: 12px;
  line-height: 18px;
  color: var(--white-two);
  margin: auto 12px 9px 13px;
}

.medicalNewsTop .myMedicalNewsImgArea .myMedicalNews-firstP{
  height: 29px;
  font-size: 20px;
  font-weight: 300;
  color: var(--white-two);
  position: relative;
  margin: 13px auto 3px 12px;
}

.medicalNewsTop .myMedicalNewsImgArea .myMedicalNews-secondP{
  height: 36px;
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  color: var(--white-two);
  margin: auto 13px 9px 12px;
}

.medicalNewsTop .newsFeedSettingText{
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  color: var(--brownish-grey);
  padding: 10px 13px 20px 12px;
  display: inline-block;
  background-color: var(--white-two);
}

.medicalNewsTop .newsFeedSettingText .textMarginCol{
  margin-bottom: 30px;
}

.medicalNewsTop .newsFeedSettingText .settingImg{
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
}

.medicalNewsTop .newsFeedSettingText .settingImg img{
  width: 16px;
  height: 16px;
  margin-top: 4px;
}
</style>
